	module.exports = {"data":{"site":{"siteMetadata":{"title":"Diogo Alves","author":"Diogo Alves"}},"markdownRemark":{"id":"/home/diogoalves/work/diogoalves.github.io/src/pages/019-mentalidade-de-crescimento/index.md absPath of file >>> MarkdownRemark","html":"<p>Recebi há alguns dias um link para o texto <a href=\"https://www.khanacademy.org/talks-and-interviews/conversations-with-sal/a/the-learning-myth-why-ill-never-tell-my-son-hes-smart\">The learning myth: Why I’ll never tell my son he’s smart</a>, em <a href=\"http://www.medclick.com.br/saude/2015/01/o-mito-da-aprendizagem-porque-nunca-dizer-para-seu-filho-que-ele-e-inteligente/\">portugês aqui</a>. O texto se direciona a pais procurando desenvolver uma mentalidade de crescimento nas crianças.</p>\n<p>Achei o paradigma tão válido para mim que fui procurar algum material de apoio para adultos e achei esse <a href=\"http://trainugly.com/mindset/\">playbook</a>. É um paradigma de aprendizado.  </p>\n<h4><a href=\"http://trainugly.com/portfolio/the-jungle-tiger/\">Learning Like a Jungle Tiger</a></h4>\n<blockquote>\n<p>The easy and safe approach led to no learning, no growth.</p>\n</blockquote>\n<blockquote>\n<p>We learn best when we operate in edge of our habilities. A little bit outside of our comfort zone. When strech and challenge.</p>\n</blockquote>\n<p>Paralelo da zona de conforto com a jaula de um tigre no zoológico.</p>\n<p>Me sinto afetado quando ele cita “Eu não danço” como exemplo de decisão de permanecer na zona de conforto.</p>","frontmatter":{"title":"Mentalidade de crescimento","date":"December 15, 2017"}}},"pathContext":{"slug":"/019-mentalidade-de-crescimento/"}}

